.content{
    border: 0.1rem solid black;
    padding: 1rem;
    text-align: center;
    margin-top: 1rem;
    background: -webkit-gradient(linear,left top, right bottom,from(#999999),color-stop(#AAAAAA),color-stop(#CCCCCC),color-stop(#EEEEEE),color-stop(#f4f4f4),to(white));
    background: -webkit-linear-gradient(top left,#999999,#AAAAAA,#CCCCCC,#EEEEEE,#f4f4f4,white);
    background: linear-gradient(to bottom right,#999999,#AAAAAA,#CCCCCC,#EEEEEE,#f4f4f4,white);
    background-repeat: no-repeat;
    background-attachment: fixed;
    opacity: 0.8;
    width:60%
}

.scroll{
    overflow: scroll;
    border: 1px solid black;
    height: 500px;
    margin: 20px;
}

