.logo{
    height:10rem;
    width:10rem;
}

.title{
    position: absolute;
    color:green;
    top:1rem;
    left:1rem;
}

.title img{
    border:1px solid black;
    background: -webkit-gradient(linear,left top, right bottom,from(black),color-stop(gray),to(black));
    background: -webkit-linear-gradient(top left,black,gray,black);
    background: linear-gradient(to bottom right,black,gray,black);
}

.title h1{
    font-family: 'Acme', sans-serif;
    letter-spacing: 2px;
}

@media screen and (max-width:850px){
    .logo{
        height:7rem;
        width:7rem;
    }
}

@media screen and (max-width:650px){
    .logo{
        height:6rem;
        width:6rem;
    }
    .title h1{
        font-family: 'Acme', sans-serif;
        letter-spacing: 1px;
        position: absolute;
        top:-30px;
        left:110px
    }
}

